.main-footer__wrapper {
  margin-top: 8px;
  padding: 33px 18px;

  @media (min-width: @tablet-width) {
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    padding: 36px 43px 36px 60px;
  }

  @media (min-width: @desktop-width) {
    padding: 36px 120px;
  }
}

.main-footer__hashtag {
  font-size: 21px;
  font-weight: 700;
  line-height: 26px;
  text-align: center;
}

.main-footer__copyright {
  display: flex;
  justify-content: center;
}

.main-footer__social {
  margin-top: 30px;
  margin-bottom: 30px;
}
